
/**/
const Tool =  resolve => require(['@/views/tool/Tool.vue'],resolve)
const Rule =  resolve => require(['@/views/tool/Rule.vue'],resolve)
const Create = resolve => require(['@/views/tool/Create.vue'],resolve)
const Management =  resolve => require(['@/views/active/Management.vue'],resolve)
const Attend =  resolve => require(['@/views/active/Attend.vue'],resolve)

const routers = [
    { path: '/marketing-center/tool', component: Tool, name: '' ,
        children: [
            { path: '/marketing-center/tool/detail', component: Rule, name: ''},
            { path: '/marketing-center/tool/create', component: Create, name: ''}
        ]
    },
    { path: '/marketing-center/management', component: Management, name: '' ,
        children: [
            { path: '/marketing-center/management/detail', component: Create, name:''},
            { path: '/marketing-center/management/attend', component: Attend, name:''}
        ]
    }
]


export default routers;



