
/**/
const Orders =  resolve => require(['@/views/transaction/Orders.vue'],resolve)
const OrderDetail =  resolve => require(['@/views/transaction/OrderDetail.vue'],resolve)
const ServiceOrder =  resolve => require(['@/views/transaction/ServiceOrders.vue'],resolve)
const ServiceDetail =  resolve => require(['@/views/transaction/ServiceDetail.vue'],resolve)
const Coupon =  resolve => require(['@/views/transaction/Coupon.vue'],resolve)
const Reservation =  resolve => require(['@/views/transaction/Reservation.vue'],resolve)
const ReservationDetail =  resolve => require(['@/views/transaction/ReservationDetail.vue'],resolve)
const Bind =  resolve => require(['@/views/transaction/Bind.vue'],resolve)

const routers = [
        { path: '/transaction/orders', component: Orders, name: '' ,
            children: [
                { path: '/transaction/orders/order-detail', component: OrderDetail, name: '' },
            ]
        },
        { path: '/transaction/service-orders', component: ServiceOrder, name: '',
            children: [
                { path: '/transaction/service-orders/service-detail', component: ServiceDetail, name: '' },
            ]
        },
        { path: '/transaction/coupon', component: Coupon, name: '' },
        { path: '/transaction/reservation', component: Reservation, name: '' ,
            children: [
                { path: '/transaction/reservation/reservation-detail', component: ReservationDetail, name: '' },
            ]
        },
        { path: '/transaction/bind', component: Bind, name: '' }
]


export default routers;



